<script>
import dialogCom from "@/components/OptionalCharges/dialogCom";

export default {
  data() {
    return {
      selectedId: [],
      product_name: "",
      Loading: false,
      show: true,
      isSelectedList1: false,
      isSelectedList2: true,
      pageSize: 50,
      pagerCount: 5,
      currentPage: 1,
      total: 0,
      count: 0,
      checkedimagesList: [], //已选中
      multipleSelection: [],
      tableData: [],
      //商品大图
      bigImg: [],
      //商品弹出框数据
      passValue: {
        //商品信息弹出框显示状态
        showState: false,
        val: {},
      },
    };
  },
  methods: {
    changeImg(index, data) {
      document.getElementsByClassName("imgShow")[index].src = data;
    },
    handleClickSearch() {
      this.$store.commit("SEARCH_VALUE", this.product_name);
    },
    changeListStatus(status) {
      if (status == true) {
        this.isSelectedList1 = true;
        this.isSelectedList2 = false;
      } else {
        this.isSelectedList2 = true;
        this.isSelectedList1 = false;
      }

      this.show = status;
      localStorage.setItem("showStatus", status);

      this.$emit("changeShowStatus", this.show);
      // this.$router.go(0)
    },
    // 点击下边小图换主图
    changeImage(index, path) {
      this.tableData[index].mainImgPath = path;
    },
    // 点击左边的按钮往左走
    leftClick(index) {
      var i = this.tableData[index].mainImgIndex - 1;
      this.$set(this.tableData[index], "mainImgIndex", i);
    },
    // 点击左边的按钮往左走
    rightClick(index) {
      var i = this.tableData[index].mainImgIndex + 1;
      this.$set(this.tableData[index], "mainImgIndex", i);
    },
    // 查看详情
    clickDetail(val) {
      this.passValue.showState = true;
      this.passValue.val = val;
    },
    // 关闭弹出框
    closeDialog(value) {
      this.passValue.showState = value;
    },
    // 添加至商品
    addThisProduct(id) {
      this.$router.push({ name: "Design", params: { productid: id } });
      // if (this.multipleSelection.length > 1) {
      //   this.$message({
      //     type: 'warning',
      //     message: '只能选择一件商品进行设计'
      //  })
      //  } else if(this.multipleSelection.length == 0) {
      //    this.$message({
      //      type: 'warning',
      //      message: '请选择一件商品进行设计'
      //  })
      //  }else {
      //     addShop = this.multipleSelection.join(",");
      //  this.postRequest(`/v1/productaddgoods?product_id=${addShop}`).then(
      //    res => {
      //      if(res.data.code == 200) {
      //  window.location.href = `http://design.321.design/index/index/hicustom/?productid=${addShop}&userid=${sessionStorage.getItem("selectUId")}`;
      // }
      // }
      // );
      //  }
    },
    // 多选按钮发生改变
    handleCheckedimagesListChange(val) {
      let arr = val.map(function(item, index, val) {
        return item.id;
      }, this);
      this.selectedId = arr;
      // this.checkedimagesList = value
      // this.multipleSelection = []
      // for (const key in value) {
      //   if (value.hasOwnProperty(key)) {
      //     const element = value[key]
      //     this.multipleSelection.push(element.id)
      //   }
      // }
    },
    // 点击刷新按钮
    handleClickRefresh() {
      this.initData();
    },
    //每页多少条发生改变
    handleSizeChange(value) {
      this.$store.commit("CHANGE_CATE_LIMIT", value);
      // this.pageSize = value;
      this.initData();
    },
    handleCurrentChange(value) {
      this.$store.commit("CHANGE_CATE_PAGE", value);
      this.currentPage = value;
      this.initData();
    },
    async initData() {
      this.Loading = true;
      let result = await this.api.productList({
        cate_id: `${this.$store.state.productCate.cate_id}`,
        page: `${this.currentPage}`,
        limit: `${this.pageSize}`,
        product_name: `${this.$store.state.productSearchVal}`,
      });
      this.total = result.data.data.total;
      this.tableData = result.data.data.data;
      for (var i = 0; i < this.tableData.length; i++) {
        this.$set(
          this.tableData[i],
          "mainImgPath",
          this.tableData[i].product_imgs[0]
        );
        this.$set(this.tableData[i], "mainImgIndex", 0);
      }
      this.count = result.data.data.count;
      for (let index = 0; index < this.tableData.length; index++) {
        const element = this.tableData[index];
        for (let n = 0; n < element.product_imgs.length; n++) {
          // this.bigImg.push(element.product_imgs[0].texture);
          if (element.product_imgs[n].master == 1) {
            this.bigImg.push(element.product_imgs[n].texture);
          }
        }
      }
      this.Loading = false;
    },
  },
  created() {
    this.$store.commit("CHANGE_IMGLIST_CATE_ID", 0);
  },
  computed: {
    counts() {
      return this.$store.state.productCate.cate_id;
    },
    searchValue() {
      return this.$store.state.productSearchVal;
    },
  },
  watch: {
    counts: function(newVal, oldVal) {
      sessionStorage.setItem("oldCate_id", newVal); //防止用户刷新页面时因为cate_id被刷新掉而进行重复请求
      this.initData();
    },
    searchValue(newVal, oldVal) {
      this.initData();
    },
  },
  beforeMount() {
    if (sessionStorage.getItem("cate_id")) {
      this.$store.commit("CHANGE_CATE_ID", sessionStorage.getItem("cate_id"));
    }
    this.initData();
  },
  components: {
    dialogCom,
  },
};
</script>

