<template>
  <div>
    <el-dialog
      title="提示"
      :visible.sync="passValue.showState"
      width="70%"
      :before-close="handleClose"
      class="addProTip"
    >
      <el-tabs v-model="activeName" type="card">
        <el-tab-pane label="基础信息" name="first">
          <el-form
            ref="form"
            :model="passValue"
            label-width="120px"
            size="mini"
            disabled
            v-if="passValue.showState"
          >
            <el-form-item label="名称">
              <el-input v-model="passValue.val.name"></el-input>
            </el-form-item>
            <el-form-item label="标签">
              <el-input v-model="passValue.val.name"></el-input>
            </el-form-item>
            <el-form-item label="描述">
              <el-input type="textarea" v-model="passValue.val.description"></el-input>
            </el-form-item>
            <el-form-item label="货号">
              <el-input v-model="passValue.val.product_code"></el-input>
            </el-form-item>
            <el-form-item label="库存">
              <el-input v-model="passValue.val.inventory"></el-input>
            </el-form-item>
            <el-form-item label="MSRP：" class="marginBottm5 formBottomConRight">
              <el-input v-model="passValue.val.msrp" autocomplete="off">
                <template slot="append">美元</template>
              </el-input>
            </el-form-item>
            <el-form-item label="重量：" class="marginBottm5 formBottomConRight">
              <el-input v-model="passValue.val.product_weight" autocomplete="off">
                <template slot="append">克</template>
              </el-input>
            </el-form-item>
            <el-form-item label="运费：" class="marginBottm5 formBottomConRight">
              <el-input v-model="passValue.val.freight" autocomplete="off">
                <template slot="append">人民币</template>
              </el-input>
            </el-form-item>
            <el-form-item label="发货周期：" class="marginBottm5 formBottomConRight textLeft">
              <el-input-number
                v-model="passValue.val.delivery_end"
                controls-position="right"
              >
              </el-input-number>~
              <el-input-number
                v-model="passValue.val.delivery_start"
                controls-position="right"
              >
              </el-input-number>天
            </el-form-item>
          </el-form>
        </el-tab-pane>
        <el-tab-pane label="颜色/规格" name="second">
          <div class="colorCon">
            <el-table
              :data="passValue.val.product_color"
              style="width: 100%"
              :default-sort="{prop: 'date', order: 'descending'}"
              class="positonNone"
            >
              <el-table-column type="index" header-align="center" width="50"></el-table-column>
              <el-table-column label="图片" min-width="70">
                <template slot-scope="scope">
                  <span v-if="passValue.val.product_imgs.length>0" effect="light">
                    <el-tooltip placement="right-start">
                      <div slot="content">
                        <img v-lazy="bigImgSrc" alt srcset class="bigImgCon">
                      </div>
                      <img v-lazy="passValue.val.product_imgs[0].path" class="ImgCon" alt srcset ref="user_image"  @mouseover="mouseover(passValue.val.product_imgs)" @mousewheel="wheel(passValue.val.product_imgs)">
                    </el-tooltip>
                  </span>
                </template>
              </el-table-column>
              <el-table-column prop="color_name" label="颜色" min-width="70"></el-table-column>
              <el-table-column label="主色" min-width="70">
                <template slot-scope="scope">
                  <i class="el-icon-check colorGreen" v-if="scope.row.main==1"></i>
                  <i class="el-icon-close colorRed" v-if="!scope.row.main==1"></i>
                </template>
              </el-table-column>
              <el-table-column label="上架" min-width="70">
                <template slot-scope="scope">
                  <el-tooltip class="item" effect="dark" content="上架" placement="top">
                    <i class="el-icon-check colorGreen" v-if="scope.row.status==1"></i>
                  </el-tooltip>
                  <el-tooltip class="item" effect="dark" content="未上架" placement="top">
                    <i class="el-icon-close colorRed" v-if="!scope.row.status==1"></i>
                  </el-tooltip>
                </template>
              </el-table-column>
            </el-table>
          </div>
          <div class="specCon">
            <el-table
              :data="passValue.val.product_size"
              style="width: 100%"
              :default-sort="{prop: 'date', order: 'descending'}"
              class="positonNone"
            >
              <el-table-column type="index" header-align="center" width="50"></el-table-column>
              <el-table-column prop="size" label="规格"></el-table-column>
              <el-table-column prop="putaway_status" label="上架">
                <template slot-scope="scope">
                  <el-tooltip class="item" effect="dark" content="上架" placement="top">
                    <i class="el-icon-check colorGreen" v-if="scope.row.putaway_status==1"></i>
                  </el-tooltip>
                  <el-tooltip class="item" effect="dark" content="未上架" placement="top">
                    <i class="el-icon-close colorRed" v-if="!scope.row.putaway_status==1"></i>
                  </el-tooltip>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </el-tab-pane>
        <el-tab-pane label="SKU详情" name="third">
          <el-table
            :data="passValue.val.product_size"
            style="width: 100%"
            :default-sort="{prop: 'color', order: 'descending'}"
            class="positonNone"
          >
            <el-table-column prop="sku" label="SKU编码"></el-table-column>
            <el-table-column prop="color" sortable label="颜色"></el-table-column>
            <el-table-column prop="size" sortable label="规格"></el-table-column>
            <el-table-column prop="putaway_status" label="上架">
              <template slot-scope="scope">
                <el-tooltip class="item" effect="dark" content="上架" placement="top">
                  <i class="el-icon-check colorGreen" v-if="scope.row.putaway_status==1"></i>
                </el-tooltip>
                <el-tooltip class="item" effect="dark" content="未上架" placement="top">
                  <i class="el-icon-close colorRed" v-if="!scope.row.putaway_status==1"></i>
                </el-tooltip>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
      <span slot="footer" class="dialog-footer">
        <!-- <el-button size="small" @click="handleClose">取 消</el-button>
        <el-button size="small" type="primary" icon="el-icon-plus" @click="clickAddThisShop">添加该商品</el-button> -->
      </span>
    </el-dialog>
  </div>
</template>
<script>
  let imgIndex = 0;//bigImg索引
export default {
  props: ['dialogValue'],
  data(){
    return {
      bigImgSrc: '',//商品图片
      activeName: "first", //标签页默认显示
      passValue: {
        showState: false,
        val: {}
      }
    }
  },
  methods: {
    //悬浮商品图片并滚动
    wheel(imgValue,e=event) {
      //判断浏览器，谷歌|| firefox滑轮事件
      if (e.wheelDelta > 0 || e.detail > 0) {
        //当滑轮向上滚动时
        if(imgValue.length>=imgIndex&&imgIndex>=0){
          this.bigImgSrc = imgValue[imgIndex].path;
          imgIndex--;
        }
        if(imgIndex==-1){
          imgIndex = 3;
        }
      }
      if (e.wheelDelta < 0 || e.detail < 0) {
        //当滑轮向下滚动时
        if(imgValue.length>imgIndex){
          this.bigImgSrc = imgValue[imgIndex].path;
          imgIndex++;
        }
        if(imgValue.length==imgIndex){
          imgIndex = 0;
        }
      }
    },
    mouseover(val){
      this.bigImgSrc = val[0].path;
    },
    //关闭弹出框
    handleClose() {
      this.$emit('closeDialog',false);
    },
    //添加该商品（单个商品）
    clickAddThisShop(){
      this.postRequest(`/v1/productaddgoods?product_id=${this.passValue.val.id}`)
      .then((res)=>{
        if(res.data.code==200){
          this.$message({
            message: '添加成功，请前往商品管理查看',
            type: 'success'
          });
          this.handleClose();
        }
      })
    },
  },
  watch: {
    dialogValue: {
      deep: true,
      handler: function(newVal){
        this.passValue = newVal;
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.colorCon {
  width: 47%;
  margin: 0 1%;
  display: inline-block;
  vertical-align: top;
  .ImgCon {
    width: 50px;
    vertical-align: top;
    margin: 5px 0;
    cursor: pointer;
  }
}
.specCon {
  display: inline-block;
  width: 45%;
  vertical-align: top;
}
</style>