                <!-- <img v-lazy="scope.row.product_imgs[0].coverImg+'?imageView2/2/w/500'" alt srcset ref="user_image" @mouseover="mouseover(scope.row.product_imgs)" @mousewheel="wheel(scope.row.product_imgs)"> -->
