<script>
  let imgIndex = 0;//bigImg索引
  import dialogCom from '@/components/OptionalCharges/dialogCom'
export default {
  data() {
    return {
      product_name:"",
      isSelectedList1: true,
      isSelectedList2: false,
      currentPage: 1, //第几页
      pagerCount: 5, //页码按钮数量
      total: null, //共有多少条
      pageSize: 50,//每页多少条
      loading: true, //加载状态
      tableData: [], //可选产品列表
      bigImgSrc: '',//商品图片
      count: null,//共几笔
      multipleSelection: [],//多选添加商品集合
      //商品弹出框数据
      passValue: {
        //商品信息弹出框显示状态
        showState: false,
        val: {}
      },
    };
  },
  methods: {
    handleClickSearch(){
      this.$store.commit('SEARCH_VALUE',this.product_name);
    },
      changeListStatus(status) {
      if(status==true) {
        this.isSelectedList1 = true
        this.isSelectedList2 = false
      }else {
        this.isSelectedList2 = true
        this.isSelectedList1 = false
      }
      
      this.show = status
      localStorage.setItem('showStatus',status);
      this.$emit('changeShowStatus',this.show);
      // this.$router.go(0)
    },
    handleSizeChange(val) {
      this.$store.commit('CHANGE_CATE_LIMIT',val);
      // this.requestData.limit = val;
      this.initData();
    },
    handleCurrentChange(val) {
    //  console.log(val);
      this.currentPage=val;
   //     console.log(this.currentPage);
      this.$store.commit('CHANGE_CATE_PAGE',val);
      this.initData();
    },
    handleSelectionChange(val) {
      this.multipleSelection = [];
      for (const key in val) {
        if (val.hasOwnProperty(key)) {
          const element = val[key];
          this.multipleSelection.push(element.id); 
        }
      }
    },
    //悬浮商品图片并滚动
    wheel(imgValue,e=event) {
      //判断浏览器，谷歌|| firefox滑轮事件
      if (e.wheelDelta > 0 || e.detail > 0) {
        //当滑轮向上滚动时
        if(imgValue.length>=imgIndex&&imgIndex>=0){
          this.bigImgSrc = imgValue[imgIndex];
          imgIndex--;
        }
        if(imgIndex==-1){
          imgIndex = 3;
        }
      }
      if (e.wheelDelta < 0 || e.detail < 0) {
        //当滑轮向下滚动时
        if(imgValue.length>imgIndex){
          this.bigImgSrc = imgValue[imgIndex];
          imgIndex++;
        }
        if(imgValue.length==imgIndex){
          imgIndex = 0;
        }
      }
    },
    mouseover(val){
      this.bigImgSrc = val[0];
    },
    //双击table->row
    handledbClickRow(val) {
      this.passValue.showState = true;
      this.passValue.val = val;
    },
    // 关闭弹出框
    closeDialog(value){
      this.passValue.showState = value;
    },
    //添加至商品按钮（多选添加按钮）
    addThisProduct(id) {
      this.$router.push({name:'DesignPage',params:{productid:id}})
    // if (this.multipleSelection.length > 1) {
      //   this.$message({
      //     type: 'warning',
      //     message: '只能选择一件商品进行设计'
      //  })
      //  } else if(this.multipleSelection.length == 0) {
      //    this.$message({
      //      type: 'warning',
      //      message: '请选择一件商品进行设计'
      //  })
      //  }else {
    //     addShop = this.multipleSelection.join(",");
        //  this.postRequest(`/v1/productaddgoods?product_id=${addShop}`).then(
        //    res => {
        //     if(res.data.code == 200) {
            //  window.location.href = `http://design.321.design/index/index/hicustom/?productid=${addShop}&userid=${sessionStorage.getItem("selectUId")}`;
            // }
          // }
        // );

    },
    //刷新
    handleClickRefresh() {
      this.initData();
    },
    //初始化数据
    async initData() {
      this.loading = true;
      let res = await this.api.productList({
        cate_id: `${this.$store.state.productCate.cate_id}`,
        page: `${this.currentPage}`,
        limit: `${this.pageSize}`,
        product_name: `${this.$store.state.productSearchVal}`
      })
      if(res.data.code=='200'){
        this.tableData = res.data.data.data;
        this.count = res.data.data.count;
        this.total = res.data.data.total;
        this.loading = false;
      }
    }
  },

  computed: {
    counts(){
      return this.$store.state.productCate.cate_id;
    },
    searchValue(){
      return this.$store.state.productSearchVal;
    }
  },
  watch: {
    counts: function(newVal,oldVal){
      sessionStorage.setItem('oldCate_id',newVal);//防止用户刷新页面时因为cate_id被刷新掉而进行重复请求
      this.initData();
    },
    searchValue(newVal,oldVal){
      this.initData();
    }
  },
  beforeMount() {
    if(sessionStorage.getItem('cate_id')){
      this.$store.commit('CHANGE_CATE_ID',sessionStorage.getItem('cate_id'));
    }
    this.initData();
  },
  components: {
    dialogCom
  }
};
</script>


