/*可选产品页面*/
<template>
  <div class="d-flex">
    <catalogueOfProducts :componentName='componentName' />
    <pageMain v-if="showStatus"  @changeShowStatus="changeShowStatus" class="flex-1"  />
    <pageOtherMain v-if="!showStatus"  @changeShowStatus="changeShowStatus" class="flex-1" />
  </div>
</template>

<script>
  
  import catalogueOfProducts from '@/components/OptionalCharges/catalogueOfProducts'//产品目录页面
  import pageMain from '@/components/OptionalCharges/main'//可选产品主体页面
  import pageOtherMain from '@/components/OptionalCharges/pageOtherMain'
  
export default {
  data(){
    return {
      showStatus: true,
      componentName: 'OptionalCharges',
    }
  },
  methods: {
    changeShowStatus(value){
      this.showStatus = value;
    }
  },
  created(){
    if(localStorage.getItem('showStatus')=='true'){
      this.showStatus = true;
    }
    if(localStorage.getItem('showStatus')=='false'){
      this.showStatus = false;
    }
  },
  destroyed() {
   let UID = sessionStorage.getItem('userId')
   sessionStorage.setItem('selectUId', UID)
  },
  components: {
    
    catalogueOfProducts,
    pageMain,
    pageOtherMain
  }
}
</script>

