/*产品分类目录*/
<template>
  <div class="treeCon">
      <div class="tree" v-show="iconProductState" @mouseover="mouseoverTree" @mouseout="mouseoutTree">
        <div class="treeTitle d-flex fs-xl jc-start">
        <i class="iconfont icon_mulu fs-xl"></i>
        <div style="margin-left:.5em">产品分类目录</div>
      </div>
        <el-tree
        class="tree1"
          v-loading='proCateLoading'
          style="padding-left: 5px;"
          :data="treeData"
          :props="defaultProps"
          :highlight-current="true"
          :expand-on-click-node="false"
          :default-expand-all="false"
          @node-click="handleNodeClick">
        </el-tree>
      </div>
  </div>
</template>

<script>
export default {
  props: ['componentName'],
  data() {
    return {
      proCateLoading:false,
      // iconProductState: false,//产品列表显示状态
      iconProductState: true,//产品列表显示状态
      treeData: [],//tree节点值
      defaultProps: {
        children: 'subcate',
        label: 'cate_name'
      },
      cate_id: '',//商品分类目录
    };
  },
  methods: {
    handleNodeClick(data) {
      if(this.cate_id != data.id){
        this.cate_id = data.id;
        if(this.componentName=='OptionalCharges'){
          this.$store.commit('CHANGE_CATE_ID',data.id);
        }
        if(this.componentName=='CommodityManagement'){
          this.$store.commit('CHANGE_SHOP_PRODUCT_CATE_ID',data.id);
        }
        if(this.componentName=='OrderManagement'){
          this.$store.commit('CHANGE_ORDER_CATEID',data.id);
        }
        // ;
        // ;
      }else {
        return false;
      }
    },
    //修改产品列表目录状态
    clickIconProductState(){
      this.iconProductState = true;
    },
    mouseoutTree(){
      // this.iconProductState = false;
      this.iconProductState = true;
    },
    mouseoverTree(){
      this.iconProductState = true;
    },
    async initData(){
      this.proCateLoading = true
      // 获取产品分类目录列表
      let res = await this.api.leftmenu()
      if(res.data.code=='200'){
        
        for (const key in res.data.data) {
          if (res.data.data.hasOwnProperty(key)) {
            const element = res.data.data[key];
            this.treeData.push(element);
          }
        }
        this.proCateLoading = false
      }
    },
  },
  beforeMount(){
    this.initData();
  },
  created() {
    this.$store.commit('CHANGE_CATE_ID',0);
  }
};
</script>

<style lang="scss" scoped>
.treeCon {
  border:1px solid #dcdfe6;
  width: 200px;
  background-color: #fff;
  .treeTitle {
    padding-left: 1em;
    height: 2.5em;
    line-height: 2.5em;
    color: #333;
    background-color: #f9f9f9;
    border-bottom: 1px solid #dcdfe6;
  }
  .tree1{
    height: calc(100vh - 11em);overflow: auto;
  }
}
</style>
